@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Merriweather:ital@1&family=Raleway&family=Space+Mono&family=Work+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Merriweather:ital@1&family=Open+Sans:wght@600;700&family=Raleway:ital,wght@0,500;0,600;0,700;1,400&family=Space+Mono&display=swap");
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  list-style: none;
}

html, body {
  margin: 0 auto;
  background-color: #E5E5E5;
}

/*TABS SECTION*/
.tabs {
  width: 1170px;
  margin: 0 auto;
  background-color: #FFFFFF;
}

.tabs__container > input[type="radio"] {
  display: none;
}

.tabs__container > div {
  /* скрыть контент по умолчанию */
  display: none;
  border: 1px solid #E5E5E5;
  padding: 0 0 0 100px;
  font-size: 16px;
  width: 100%;
}

#tabChoice1:checked ~ #tabs__tab1-content,
#tabChoice2:checked ~ #tabs__tab2-content,
#tabChoice3:checked ~ #tabs__tab3-content {
  display: block;
  width: 100%;
  min-height: 501px;
}

.tabs__container > label {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-color: #f5f5f5;
  border: 1px solid #e0e0e0;
  padding: 10px 60px;
  font-size: 14px;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  line-height: 19px;
  text-transform: uppercase;
  -webkit-transition: color 0.15s ease-in-out;
  transition: color 0.15s ease-in-out;
  background-color: 0.15s ease-in-out;
  cursor: pointer;
  position: relative;
  top: 1px;
}

.tabs__container > label:not(:first-of-type) {
  border: none;
  background-color: #fff;
}

.tabs__container > input[type="radio"]:checked + label {
  background-color: #fff;
  border-bottom: 1px solid #fff;
}

/*tab1_content */
.tab1-content__main-title {
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
  font-size: 30px;
  display: inline-block;
}

.tab1-content__secondary-title {
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-size: 18px;
  display: inline-block;
}

.button {
  display: block;
  width: 270px;
  height: 60px;
  margin-bottom: 30px;
  border: none;
  border-color: transparent;
}

.tab1-content__button {
  background-color: #EC7979;
  color: #fff;
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  font-size: 18px;
  text-align: center;
  -webkit-transition: background-color .3s ease-in-out;
  transition: background-color .3s ease-in-out;
}

.tab1-content__button:hover, .tab1-content__button:focus {
  cursor: pointer;
  background-color: #e75454;
}

.tab1-content__image {
  width: 100%;
  height: 100%;
}

.tab1-content__image-wrapper {
  display: inline-block;
  width: 470px;
  height: 500px;
}

.tab1-content__content-wrapper {
  display: block;
  width: 570px;
  height: 500px;
  float: left;
  margin: 0 28px 0 0;
}

/*TABS SECTION END*/
/*AWESOME SECTION*/
